@import url(../../node_modules/perfect-scrollbar/css/perfect-scrollbar.css);
html {
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

html, body {
  font-family: 'Vesper Libre', serif; }

h1 {
  margin: 0;
  line-height: inherit; }

h2 {
  margin: 0;
  line-height: inherit; }

h3 {
  margin: 0;
  line-height: inherit; }

h4 {
  margin: 0;
  line-height: inherit; }

h5 {
  margin: 0;
  line-height: inherit; }

h6 {
  margin: 0;
  line-height: inherit; }

p {
  margin: 0; }

body {
  font-size: 100%; }

h1 {
  font-size: 36px;
  font-size: 2.25rem; }

h2 {
  font-size: 30px;
  font-size: 1.875rem; }

h3 {
  font-size: 24px;
  font-size: 1.5rem; }

h4, p, label, input, menuitem, button {
  font-size: 16px;
  font-size: 1rem; }

h5, small {
  font-size: 14px;
  font-size: 0.875rem; }

h6 {
  font-size: 11px;
  font-size: 0.6875rem; }

button,
input,
optgroup,
select,
textarea {
  font-family: Arial, sans-serif; }

html, body {
  width: 100%;
  height: 100%; }

body {
  margin: 0;
  padding: 0; }

*::selection {
  background-color: #7ea5f1;
  color: #FFF; }

*::-moz-selection {
  background-color: #7ea5f1;
  color: #FFF; }

#root {
  min-height: 100%;
  display: flex;
  flex-direction: column; }

a {
  text-decoration: none; }

ul, menu, dir {
  margin: 0;
  padding: 0; }

img {
  max-width: 100%; }

.weight_100 {
  font-weight: 100; }

.weight_200 {
  font-weight: 200; }

.weight_300 {
  font-weight: 300; }

.weight_400 {
  font-weight: 400; }

.weight_500 {
  font-weight: 500; }

.weight_600 {
  font-weight: 600; }

.weight_700 {
  font-weight: 700; }

.weight_800 {
  font-weight: 800; }

.weight_900 {
  font-weight: 900; }

footer {
  color: #969696;
  font-size: 12px; }

main {
  flex-grow: 1; }

.container {
  width: 800px;
  max-width: calc(100% - 60px);
  margin: 0 auto; }

* {
  box-sizing: border-box; }
  p ~ * {
    margin-top: 10px; }

[disabled],
.is-disabled .Select-control {
  cursor: not-allowed;
  opacity: .7; }

.btn {
  padding: 10px;
  min-width: 150px;
  background-color: #FFF;
  outline: none;
  border: solid 1px #cfcfcf;
  border-radius: 3px;
  transition: all 0.1s ease-out; }
  .btn:enabled {
    cursor: pointer; }
  .btn:enabled:hover {
    background-color: #969696;
    border-color: #969696;
    color: #FFF;
    transition: all 0.1s ease-out; }

ul li {
  list-style-type: none; }

.svg__icon {
  max-width: 100%;
  display: inline-block; }
  .svg__icon svg {
    max-width: 100%;
    display: block; }
  .svg__icon.xs {
    width: 10px;
    height: 10px; }
  .svg__icon.sm {
    width: 20px;
    height: 20px; }
  .svg__icon.md {
    width: 30px;
    height: 30px; }
  .svg__icon.lg {
    width: 40px;
    height: 40px; }

.notification {
  margin: 50px 0;
  padding: 30px;
  box-shadow: 0 0 50px rgba(0, 0, 0, 0.1);
  font-size: 17px;
  text-align: center; }

.c_blue {
  color: #7ea5f1; }

body h1 {
  color: red; }

.unbutton {
  border: none;
  outline: none;
  background-color: transparent;
  padding: 0;
  margin: 0; }

.Select-value {
  border: none; }

input.placeholder {
  font-weight: 200;
  color: #969696; }

input:-moz-placeholder {
  font-weight: 200;
  color: #969696; }

input::-moz-placeholder {
  font-weight: 200;
  color: #969696; }

input:-ms-input-placeholder {
  font-weight: 200;
  color: #969696; }

input::-webkit-input-placeholder {
  font-weight: 200;
  color: #969696; }

input:focus {
  outline: none; }



